<template>
  <div class="center">
    <ul class="stage-icon">
      <CounterBarItem v-for="(value,index) in list" :key="index" :value="value"></CounterBarItem>
    </ul>
    <div class="lottie" ref="lottie"></div>
  </div>
</template>

<script>
import { update } from 'tween.js';
import lottie from 'lottie-web';

import CounterBarItem from './CounterBarItem.vue';
const weekstr = "日一二三四五六";
export default {
  name: "CounterBar",
  components: { CounterBarItem },
  props: {
    value: { type: Object, default: () => { } }
  },
  data() {
    return {
      sid: 0,
      count: 1111,
      list: [0, 0, 0, 0],
    }
  },
  methods: {
    stop() {
      if (this.sid) {
        clearInterval(this.sid);
        this.sid = 0;
      }
    },
    random() {
      this.stop();
      this.sid = setInterval(() => {
        // let num = Math.ceil(Math.random() * 10000);
        let num = this.count++;
        let arr = num.toString().split('');
        this.list = arr.map(item => Number(item));
      }, 2000);
    },
    cover(list) {
      return list.map(val => {
        if (val.toString().length == 1) {
          return "0" + val;
        }
        return val;
      });
    }
  },
  mounted() {
    const that = this;
    function animate(time) {
      const date = new Date();
      let data = {
        date: that.cover(date.toLocaleDateString().split('/')).join('-'),
        time: that.cover(date.toLocaleTimeString().replace("上午", "").replace("下午", "").split(":")).join(":"),
        week: weekstr.split("")[date.getDay()]
      };

      that.$emit("input", data);

      requestAnimationFrame(animate)
      update(time)
    }
    requestAnimationFrame(animate)
    
    this.random();

    lottie.loadAnimation({
      container: this.$refs.lottie,
      renderer: 'svg',
      // loop: true,
      autoplay: true,
      path: 'lottie/data.json'
    });
  }
};
</script>
<style lang="stylus" scoped>
.center {
  position: relative;
  overflow: hidden;

  .lottie {
    position: absolute;
    width: 112%;
    height: 112%;
    left: 0;
    top: 0;
    margin-top: -79px;
    margin-left: -44px;
    z-index: 4;
  }
}

ul.stage-icon {
  z-index: 0;
  list-style: none;
  top: 265px;
  height: 130px;
  margin: 0 0 0 17px;
  padding: 0;
  position: relative;
  display: flex;
  justify-content: center;

  li {
    margin: 0 2px;

    >>> span {
      font-family: 'digital';
    }

    &:last-child >>> {
      .stage-icon-top, .stage-icon-bottom {
        background: rgba(12, 129, 235, 0.35);
      }
    }
  }
}
</style>