<template>
  <el-row :gutter="5">
    <el-col :span="8">
      <PieChart class="item" title="CPU" :data="cpu"></PieChart>
    </el-col>
    <el-col :span="8">
      <PieChart class="item" title="内存" :data="memory"></PieChart>
    </el-col>
    <el-col :span="8">
      <PieChart class="item" title="磁盘" :data="disk"></PieChart>
    </el-col>
  </el-row>
</template>
<script>
import PieChart from './PieChart2';
export default {
  name: "HardWareChart",
  props: {
    data: { type: Object, default: () => { } }
  },
  computed: {
    cpu() {
      if (this.data && this.data.cpu) {
        return this.data.cpu;
      } else {
        return [];
      }
    },
    memory() {
      if (this.data && this.data.memory) {
        return this.data.memory;
      } else {
        return [];
      }
    },
    disk() {
      if (this.data && this.data.disk) {
        return this.data.disk;
      } else {
        return [];
      }
    }
  },
  components: {
    PieChart
  }
}
</script>
<style lang="stylus" scoped>
.item {
  width: 100%;
  height: 160px;
  display: inline-block;
}
</style>