<template>
  <div ref="chart" class="chart"></div>
</template>
<script>
export default {
  name: "LineChart",
  props: {
    names: { type: Array, default: () => [] },
    data: { type: Array, default: () => [] }
  },
  data() {
    return {
      chart: null,
    }
  },
  watch: {
    data(value) {
      const option = this.getOptions(value);
      if (this.chart && option) {
        this.chart.setOption(option);
      }
    }
  },
  methods: {
    getOptions() {
      if (!this.names || !this.data) {
        return;
      }

      let colors = ['19, 195, 221', '255, 151, 134'];
      // let colors = ['19, 195, 221', '247, 101, 43'];
      var option = {
        legend: {
          data: [],
          icon: 'rect',
          top: 0,
          right: 25,
          itemGap: 15,
          itemWidth: 10,
          itemHeight: 10,
          selectedMode: false,
          textStyle: {
            fontSize: '16',
            color: '#fff'
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985',
            },
          }
        },
        grid: {
          top: 30,
          left: 20,
          right: 30,
          bottom: 0,
          containLabel: true
        },
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            textStyle: {
              fontSize: 14,
              color: '#FFFFFF',
            }
          },
          axisTick: {
             length: 0 // 刻度线的长度
          },
          axisLine: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: ["#051d5f"],
              height: 1,
              type: 'dotted'
            }
          },
          boundaryGap: false,
          nameTextStyle: {
            padding: [24, 0, 0, 0],
            color: '#00c5d7'
          },
          nameGap: 0,
          data: this.names || []
        },
        yAxis: {
          type: 'value',
          axisLine: {
            show: false
          },
          axisTick: {
            length: 0 // 刻度线的长度
          },
          splitLine: {
            lineStyle: {
              color: ["#051d5f"],
              width: 1,
              type: 'dotted'
            }
          },
          axisLabel: {
            textStyle: {
              fontSize: 14,
              color: '#ffffff'
            }
          }
        },
        series: []
      }

      this.data.forEach((item, index) => {
        //去掉图例
        // option.legend.data.push(item.name);
        var series = {
          name: item.name,
          stack: item.name,
          type: 'line',
          smooth: true,
          symbol: 'circle',
          symbolSize: 2,
          itemStyle: {
            normal: {
              color: {
                type: 'linear',
                x: 0, y: 1, x2: 0, y2: 0,
                colorStops: [{
                  offset: 0,
                  color: 'rgba(' + colors[index] + ', 0.35)'// 0% 处的颜色
                }, {
                  offset: 0.55,
                  color: 'rgba(' + colors[index] + ', 0.75)'// 0% 处的颜色
                }, {
                  offset: 1,
                  color: 'rgba(' + colors[index] + ', 1)' // 100% 处的颜色
                }],
                globalCoord: false // 缺省为 false
              },
              borderColor: 'rgb(' + colors[index] + ')',
              label: { show: true, color: '#626264' },
              areaStyle: {
                type: 'default',
              }
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 16,
                color: '#ffffff'
              },
              areaStyle: {
                type: 'default',
                opacity: 1
              }
            }
          },
          data: item.value
        };
        option.series.push(series);
      });
      return option;
    }
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs.chart);
    var option = this.getOptions();
    this.chart.setOption(option);

    var index = 0;
    setInterval(() => {
      if (!this.names) {
        return;
      }
      index++;
      index = index >= this.names.length ? 0 : index;
      // const index = Math.floor(Math.random() * this.names.length);
      this.chart.dispatchAction({ type: 'downplay', seriesIndex: 0 });
      this.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: index });
      this.chart.dispatchAction({ type: 'showTip', seriesIndex: 0, dataIndex: index });
    }, 5000);
  }
}
</script>
<style lang="stylus" scoped>
.chart {
  width: 100%;
  height: 240px;
}
</style>