<template>
<body>
  <Home class="home" :style="{zoom: ratio}"></Home>
</body>
</template>

<script>
import Home from './views/Home'
export default {
  name: 'app',
  components: {
    Home,
  },
  data() {
    return {
      width: 1920,
      height: 1080,
      padding: 0,
      ratio: 1,
    }
  },
  methods: {
    onResize(element) {
      const contentWidth = element.clientWidth - this.padding * 2; //新宽度
      const contentHeight = element.clientHeight - this.padding; //新高度
      let ratio = contentWidth / this.width;
      const height = this.height * ratio;
      if (height > contentHeight) {
        this.ratio = contentHeight / this.height;
      }
      console.log(',,,,,', this.ratio);
    }
  },
  mounted() {
    // this.onResize(document.documentElement);
    // window.onresize = () => {
    //   this.onResize(document.documentElement);
    // };
  }
}
</script>

