<template>
  <el-row class="disk" :gutter="10">
    <el-col :span="3" class="disk-label">硬盘</el-col>
    <el-col :span="21-span">
      <el-progress
        :text-inside="true"
        stroke-linecap="square"
        :stroke-width="26"
        :percentage="percentage"
      ></el-progress>
    </el-col>
    <el-col :span="span" class="disk-value">{{total}}GB</el-col>
  </el-row>
</template>
<script>
export default {
  name: "",
  props: {
    data: { type: Array, default: () => [] }
  },
  data() {
    return {
      span: 3
    }
  },
  computed: {
    use() {
      const item = this.data.find(item => item.name == "use");
      return item ? item.value : 0;
    },
    total() {
      const item = this.data.find(item => item.name == "total");
      const total = item ? item.value : 1;
      this.span = total > 99 ? total > 9999 ? total > 999999 ? 6 : 5 : 4 : 3;
      return total;
    },
    percentage() {
      return Math.floor(this.use / this.total * 100);
    }
  },
}
</script>
<style lang="stylus" scoped>
.disk {
  color: #FFF;
  font-size: 20px;

  .disk-label, .disk-value {
    text-align: center;
  }

  .el-progress >>> .el-progress-bar__outer {
    border-radius: 2px;
    background: #052471;

    .el-progress-bar__inner {
      border-radius: 2px;
      // text-align: center;
      background-color: rgb(48, 142, 237);
      background-image: linear-gradient(to right, rgb(0, 223, 227), rgb(48, 142, 237));
    }
  }
}
</style>