<script>
import Panel from './components/Panel'
import PieChart from './components/PieChart.vue'
import PieChart2 from './components/PieChart2.vue'
import BarChart from './components/BarChart.vue'
import LineChart from './components/LineChart'
import DiskCapacity from './components/DiskCapacity'
import HardWareChart from './components/HardWareChart'
import ServerStatusChart from './components/ServerStatusChart'
import CounterBar from './components/CounterBar.vue';
var mockData = require('./../../public/mock-data');

let url = '/homepage.php?Action=';
let taskList = [
  { label: 'os', name: 'os', delay: 0, default: [] },
  { label: 'computer', name: 'computer', delay: 0, default: [] },
  { label: 'antivirus', name: 'antivirus', delay: 0, default: [] },
  { label: 'cpu', name: 'cpu', delay: 0, default: [] },
  { label: 'memory', name: 'memory', delay: 0, default: [] },

  { label: 'online', name: 'online', delay: 0, default: { label: [], list: [] } },
  { label: 'disk', name: 'disk', delay: 0, default: [] },
  { label: 'topnData', name: 'topnData', delay: 0, default: [] },
  { label: 'deviceData', name: 'deviceData', delay: 0, default: [] },

  { label: 'repair', name: 'repair', delay: 0, default: [] },
  { label: 'srvData', name: 'srv', delay: 0, default: [] },
  { label: 'diskData', name: 'disk1', delay: 0, default: [] },
  { label: 'userTypeData', name: 'userTypeData', delay: 0, default: [] },
];

export default {
  name: 'Home',
  components: {
    Panel,
    PieChart,
    LineChart,
    DiskCapacity,
    CounterBar,
    ServerStatusChart,
  },
  data() {
    return {
      loading: true,
      date: {},
      healthList: { success: "健 康", warning: "一 般", danger: "危 险" },
      os: null,
      cpu: null,
      memory: null,
      disk: null,
      computer: null,
      antivirus: null,
      deviceData: [],
      userTypeData: [],
      srvData: [],
      diskData: [],
      topnData: null,
      online: {},
      repair: null
    }
  },
  methods: {
    fetch(url) {
      return new Promise((resolve, reject) => {
        var xhr = new XMLHttpRequest();
        xhr.open('get', url);
        xhr.responseType = 'json';
        xhr.onloadstart = () => { };
        xhr.onload = () => {
          resolve(xhr.response);
        }

        xhr.onprogress = (e) => { };
        xhr.onerror = (e) => {
          console.log('error ', url);
          resolve(null);
        };
        xhr.send();
      });
    },
    getInitData(callback) {
      if (process.env.NODE_ENV == 'development') {
        console.log('process.env.NODE_ENV', process.env.NODE_ENV);
        url = 'http://121.225.84.148:16670' + url;
      }

      this.fetch(url + "time").then(times => {
        console.log('delay res =>', times);

        times = times || [];
        let reqList = [];
        taskList.forEach(task => {
          let time = times.find(item => item.name === task.name);
          task.delay = time && time.value || 60;//默认值
          reqList.push(this.fetch(url + task.name));
        });

        Promise.all(reqList).then(list => {
          this.loading = false;
          for (let i = 0; i < taskList.length; i++) {
            this[taskList[i].label] = list[i] || taskList[i].default;

            setTimeout(() => {
              this.runTask(taskList[i]);
            }, i * 5000)
          }
        })
      });
    },
    runTask(task) {
      task.start = Date.now();
      task.sid = setInterval(() => {
        console.log('执行任务', task.name, task.delay, task.sid, new Date() - task.start);
        this.fetch(url + task.name).then(data => {
          if (data) {
            this[task.label] = data || this[task.label] || task.default;
          } else {
            console.warn("请求已完成，数据未更新 action=", task.name);
            this.$message({ type: 'warning', message: "请求已完成，数据未更新！" + task.name });
          }
        });
      }, task.delay * 1000);
      console.log('开始任务', task.name, task.delay);
    }
  },
  created() {
    // this.getInitData();
  },
  mounted() {
    // this.$refs.usbTableHeader.appendChild(this.$refs.usbTable.$refs.headerWrapper);
    this.$refs.healthTableHeader.appendChild(this.$refs.healthTable.$refs.headerWrapper);

    //此处为mock数据，打包后会自动移除
    const { os, computer, antivirus, deviceData, userTypeData, srv, useDisk, cpu, disk, memory, topnData, online, repair } = mockData;
    this.os = os;
    this.computer = computer;
    this.antivirus = antivirus;
    this.deviceData = deviceData;
    this.userTypeData = userTypeData;
    this.srvData = srv;
    this.diskData = useDisk;
    this.cpu = cpu;
    this.disk = disk;
    this.memory = memory;
    this.topnData = topnData;
    this.online = online;
    this.repair = repair;
    this.loading = false;
  }
}
</script>
