<template>
  <div ref="chart" class="chart"></div>
</template>
<script>
export default {
  name: "ServerStatusChart",
  props: {
    data: { type: Array, default: [] }
  },
  data() {
    return {
      chart: null
    }
  },
  watch: {
    data(value) {
      const option = this.getOptions(value);
      if (this.chart && option) {
        this.chart.setOption(option);
      }
    }
  },
  methods: {
    getOptions(data) {
      if (!data || data.length == 0) {
        return;
      }
      var titleArr = [];
      var seriesArr = [];
      data.forEach(function (item, index) {
        titleArr.push({
          text: item.value + "%",
          top: '38%',
          left: index * 50 + 24 + '%',
          textAlign: 'center',
          textStyle: {
            fontWeight: 'normal',
            fontSize: '24',
            color: '#30c792',
          },
          subtext: item.name.toUpperCase(),
          subtextStyle: {
            fontSize: 14,
            color: '#FFFFFF',
            align: 'center'
          }
        });

        seriesArr.push({
          name: item.name.toUpperCase(),
          type: 'pie',
          radius: ['70%', '85%'],
          startAngle: 225,
          color: [{
            type: 'linear',
            x: 0,
            y: 0,
            x2: 0.4,
            y2: 1,
            colorStops: [{
              offset: 0,
              color: "#00f7df" // 0% 处的颜色
            }, {
              offset: 0.5,
              color: "#00f7df" // 0% 处的颜色
            }, {
              offset: 1,
              color: "#015bf6" // 100% 处的颜色
            }],
            globalCoord: false // 缺省为 false
          }, 'none'],
          hoverAnimation: false,
          legendHoverLink: false,
          z: 10,
          labelLine: { normal: { show: false } },
          center: [index * 50 + 25 + '%', '50%'],
          data: [{ value: 75 * item.value / 100 },
          { value: 100 - (75 * item.value / 100) }]
        }, {
          name: '',
          type: 'pie',
          radius: ['70%', '85%'],
          center: [index * 50 + 25 + '%', '50%'],
          startAngle: 225,
          color: ["#052471"],
          z: 9,
          hoverAnimation: false,
          legendHoverLink: false,
          labelLine: { normal: { show: false } },
          data: [
            { value: 75, itemStyle: { normal: { color: "#052471" } } },
            { value: 25 }]
        })
      });

      return {
        title: titleArr,
        series: seriesArr,
      }
    }
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs.chart);
    // var data = [{ name: 'CPU', value: 100 },
    // { name: '内存', value: 50 }];
    // setInterval(() => {
    //   data.forEach(item => {
    //     item.value = Math.floor(Math.random() * 100);
    //   });

    //   chart.setOption(this.getOptions(data));
    // }, 1000);
    // if (this.data && this.data.length >= 0) {
    //   this.chart.setOption(this.getOptions(this.data));
    // }
  }
}
</script>
<style lang="stylus" scoped>
.chart {
  width: 480px;
  height: 180px;
}
</style>