<template>
  <div ref="chart" class="chart"></div>
</template>
<script>
export default {
  name: "PieChart",
  props: {
    title: { type: String, default: "标签" },
    data: { type: Array, default: () => [] }
  },
  data() {
    return {
      index: 0,
      chart: null,
      autoIntervalId: 0
    }
  },
  watch: {
    data(value) {
      const option = this.getOptions(value);
      if (this.chart && option) {
        this.chart.setOption(option);
        //设置默认选中高亮部分
        this.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: this.index });
      }
    }
  },
  methods: {
    getOptions(list) {
      const gap = list && list.length >= 5 ? list && list.length >= 8 ? 5 : 10 : 30;
      const colors = this.$colors.slice(2, this.$colors.length - 1);
      // 指定图表的配置项和数据
      var option = {
        tooltip: {
          trigger: 'item',
          formatter: "{d}% <br>{b}: {c}"
        },
        color: colors,
        legend: {
          orient: 'vertical',
          padding: [5, 10],
          itemGap: gap,
          itemWidth: 15,
          itemHeight: 15,
          textStyle: {
            fontSize: '16',
            color: '#FFFFFF'
          },
          formatter: (name) => {
            const item = list.find(item => item.name === name);
            return item.name.toUpperCase() + " (" + item.value + ")";
          },
          right: 15,
          y: 'center',
          selectedMode: false,
          data: []
        },
        title: {
          text: this.title,
          top: '60%',
          left: '38%',
          textAlign: 'center',
          textStyle: {
            fontWeight: 'normal',
            fontSize: '14',
            color: '#fff',
          }
        },
        series: [{
          name: this.title,
          type: 'pie',
          radius: ['70%', '85%'],
          center: ['40%', '50%'],
          avoidLabelOverlap: false,
          hoverOffset: 4,
          label: {
            normal: {
              show: false,
              formatter: '{d|{d}%}',
              // formatter: function (item) {
              //   return '{d|' + Math.round(item.percent) + '%} \n {b|' + item.name.toUpperCase() + '}';
              // },
              position: 'center',
              rich: {
                b: {
                  color: '#FFF',
                  fontSize: 18,
                },
                d: {
                  fontSize: '35',
                  color: '#30c792',
                  height: 60
                }
              }
            },
            emphasis: {
              show: true,
              textStyle: {
                color: '#FFF',
                fontSize: '30',
                formatter: '{c}台',
              }
            }
          },
          data: list || []
        }]
      };
      if (list) {
        option.legend.data = list.map(item => item.name);
      }
      return option;
    },
    addAutoEvent() {
      this.autoIntervalId = setInterval(() => {
        this.chart.dispatchAction({ type: 'downplay', seriesIndex: 0, dataIndex: this.index });
        this.index++;
        this.index = this.index >= this.getOptions(this.data).series[0].data.length ? 0 : this.index;
        this.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: this.index });
        this.chart.dispatchAction({ type: 'showTip', seriesIndex: 0, dataIndex: this.index });
      }, 5000);
    },
    closeAutoEvent() {
      if (this.autoIntervalId) {
        clearInterval(this.autoIntervalId);
      }
    }
  },
  mounted() {
    const that = this;
    this.chart = this.$echarts.init(this.$refs.chart);

    this.chart.setOption(this.getOptions(this.data));

    //设置默认选中高亮部分
    this.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: that.index });
    // 当鼠标移入时，如果不是第一项，则把当前项置为选中，如果是第一项，则设置第一项为当前项
    this.chart.on('mouseover', function (e) {
      that.closeAutoEvent();
      that.chart.dispatchAction({ type: 'downplay', seriesIndex: 0, dataIndex: that.index });
      that.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: e.dataIndex });
    });

    //当鼠标离开时，把当前项置为选中 
    this.chart.on('mouseout', function (e) {
      that.index = e.dataIndex;
      that.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: that.index });
      that.addAutoEvent();
    });

    this.addAutoEvent();

    window.onresize = () => {
      this.chart.resize();
    }
  }
}
</script>
<style lang="stylus" scoped>
.chart {
  width: 500px;
  height: 220px;
}
</style>
