<template>
  <div ref="chart" class="chart"></div>
</template>
<script>
export default {
  name: "BarChart",
  props: {
    data: { type: Array, default: () => { } }
  },
  data() {
    return {
      chart: null
    }
  },
  watch: {
    data(value) {
      const option = this.getOptions(value);
      if (this.chart && option) {
        this.chart.setOption(option);
      }
    }
  },
  methods: {
    getOptions() {
      // 指定图表的配置项和数据
      var option = {
        color: [{
          type: 'linear',
          x: 0,
          y: 0,
          x1: 0,
          y1: 1,
          colorStops: [{
            offset: 0,
            color: '#01e3ae'   // 0% 处的颜色
          }, {
            offset: 1,
            color: '#00a1e9'// 100% 处的颜色
          }],
          globalCoord: false // 缺省为 false
        }],
        tooltip: {
          trigger: 'item',
          formatter: "{b} : {c}台"
        },
        xAxis: {
          show: false,
          type: 'value'
        },
        yAxis: {
          show: true,
          type: 'category',
          boundaryGap: false,
          axisLabel: {
            formatter: '{value}',
            textStyle: {
              fontSize: '14',
              color: '#fff'
            }
          },
          axisTick: {
            show: false
          },
          axisLine: {
            show: false
          },
          z: 10,
          data: []
        },
        grid: {
          top: 20,
          left: 30,
          right: 10,
          bottom: 0,
          containLabel: true,
        },
        series: [{
          data: [],
          type: 'bar',
          barWidth: 30,
          itemStyle: {
            normal: {
              opacity: 1,
            },
            emphasis: {
              opacity: 0.75,
            }
          },
          label: {
            fontSize: 16,
            // formatter: '{c}台 ',
            formatter: function (params) {
              return params.value > 0 ? params.value + "台" : '';
            },
            show: true,
            position: 'insideRight',
            textBorderColor: '#fff',
            textBorderWidth: 1
          }
        }]
      };

      this.data && this.data.forEach(item => {
        option.yAxis.data.unshift(item.name);
        option.series[0].data.unshift(item.value);
      });

      return option;
    }
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs.chart);
    this.chart.setOption(this.getOptions());
  }
}
</script>
<style lang="stylus" scoped>
.chart {
  width: 500px;
  height: 220px;
}
</style>
